import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'HelloWorld',
        component: resolve => { require(["../components/HelloWorld.vue"], resolve) },
    }, {
        path: '/a',
        component: resolve => { require(["../components/box.1.vue"], resolve) }
    }]
})


